import { ThemeProvider, createTheme } from '@mui/material';
import { Button, CloseDeleteBtn, ModalTitle, ModalWrapper } from 'components';
import { Input, Label } from 'pages/Profile/Profile.styled';
import { useForm } from 'react-hook-form';
import { colors } from 'styles/utils/variables';
import { Form } from './ModalRegistration.styled';

const theme = createTheme({
  palette: {
    auxillary: {
      main: colors.auxiliaryText,
    },
  },
});

export function ModalRegistration({ closeModal }) {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm();

  const onSubmit = data => {
    console.log(data);
    closeModal();
  };

  return (
    <ModalWrapper closeModal={closeModal}>
      <ThemeProvider theme={theme}>
        <ModalTitle></ModalTitle>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <CloseDeleteBtn handleClick={closeModal} />
          <Label htmlFor="name">'*</Label>
          <Input
            {...register('name', {
              required: "  '",
            })}
            id="name"
            error={!!errors.name}
            helperText={errors.name?.message}
            color="auxillary"
          />
          <Label htmlFor="email">*</Label>
          <Input
            {...register('email', {
              required: "  '",
              pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
            })}
            id="email"
            error={!!errors.email}
            helperText={
              errors.email
                ? errors.email.type === 'required'
                  ? "  '"
                  : '   '
                : ''
            }
            color="auxillary"
          />
          <Label htmlFor="password">*</Label>
          <Input
            {...register('password', {
              required: "  '",
              minLength: 8,
            })}
            id="password"
            error={!!errors.password}
            helperText={
              errors.password
                ? errors.email.password === 'required'
                  ? "  '"
                  : '    8 '
                : ''
            }
            color="auxillary"
          />
          <Label htmlFor="repeatPassword"> *</Label>
          <Input
            {...register('repeatPassword', {
              required: "  '",
              validate: value => value === watch('password'),
            })}
            id="repeatPassword"
            error={!!errors.repeatPassword}
            helperText={
              errors.repeatPassword
                ? errors.repeatPassword.type === 'required'
                  ? "  '"
                  : '  '
                : ''
            }
            color="auxillary"
          />
          <Button type="submit"></Button>
        </Form>
      </ThemeProvider>
    </ModalWrapper>
  );
}
